import '../ControlPanelStyle.css';
import firebase from 'firebase';

var ChangePass;
var PassChange;
var oldpassword;
var password;
var passwordrep;
var Data;
var user;
export function PwChange(key) {
  if(document.getElementById("user-role").innerHTML === "Administratorius"){user = "Admin"}
  else if((document.getElementById("user-role").innerHTML === "Buhalteris(-)")||(document.getElementById("user-role").innerHTML === "Vadovas(-)")){user = "Staff"}
  else{user = "User"}
  return(
    console.log(key),
    oldpassword = document.getElementById('old-password').value,
    password = document.getElementById('password').value,
    passwordrep = document.getElementById('password-rep').value,
    console.log(oldpassword + " " + password + " " + passwordrep),
    ChangePass = firebase.database().ref(user).orderByKey().equalTo(key),
    ChangePass.once('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
          Data = childSnapshot.val();
      });
      if(oldpassword === Data.password){
        if(password === passwordrep){
          if((password.length >= 8)&&(password.length <= 20)){
            PassChange = firebase.database().ref(user + "/" + key);
            PassChange.update({
              password: password
            });
            document.getElementById('old-password').value = "";
            document.getElementById('password').value = "";
            document.getElementById('password-rep').value = "";
            document.getElementById("change-pw").style.color = "green";
            document.getElementById("change-pw").innerHTML = "Slaptaodis skmingai pakeistas";
            document.getElementById("old-password").innerHTML = "";
            document.getElementById("password").innerHTML = "";
            document.getElementById("password-rep").innerHTML = "";

          }
          else{
            document.getElementById("change-pw").innerHTML = "Slaptaodis turi buti nuo 8 iki 20 simboli";
            document.getElementById("change-pw").style.color = "red";
          }

        }
        else{
          document.getElementById("change-pw").innerHTML = "Slaptaodiai nesutampa";
          document.getElementById("change-pw").style.color = "red";
        }

      }
      else{
        document.getElementById("change-pw").innerHTML = "Neteisingas slaptaodis";
        document.getElementById("change-pw").style.color = "red";
      }
    })  
  );  
};

function ChangePassword(props) {
  return (
    <div className="dashboard-page">
      <h2>Slaptaodio keitimas</h2>
      <div>
        <div className="name">Dabartinis slaptaodis</div>
        <input id="old-password" placeholder="Dabartinis slaptaodis" className="input-box" type="password" maxLength="20" />
      </div>
      <div>
        <div className="password">Naujas slaptaodis</div>
        <input id="password" placeholder="Naujas slaptaodis" className="input-box " type="password" maxLength="20" />
      </div>
      <div>
        <div className="password">Pakartoti slaptaod</div>
        <input id="password-rep" placeholder="Naujas slaptaodis" className="input-box pw" type="password" maxLength="20" />
      </div>
      <div id="change-pw"></div>
      <div className="buttons">
        <button id="autobutton" className="pw-button" onClick={() => PwChange(props.id)}>Keisti slaptaod</button>
      </div>
    </div>
  );
}

export default ChangePassword;